.bg {
	width: 100% !important;
	min-height: 100vh;

	display: flex;
	justify-content: center;
	align-items: center;
}

.main {
	min-height: 100vh;

	text-align: center;
	width: min(777px, 100%);

	box-sizing: border-box;
	padding: 0 1.125rem 2rem;
}

@media (min-width: 1050px) {
	.main {
		border-right: 1px solid #bdbdbd;
		border-left: 1px solid #bdbdbd;
		padding: 0 3rem 3rem;
	}
}

.menu {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	column-gap: 1rem;
	row-gap: .4rem;
}

img {
	width: 100%;
}

a {
	color: #ffaf00;
}

a:hover {
	color: #ffffff;
	background-color: #ffaf00;
	text-decoration: none;
}